
import { defineComponent, ref, onMounted } from "vue";

const Component = defineComponent({
  setup() {
    const inputRef = ref<HTMLInputElement>();

    onMounted(() => {
      if (inputRef.value) {
        inputRef.value.focus();
      }
    });

    return {
      inputRef,
    };
  },
  props: {
    inputValue: {
      type: String,
      required: true,
    },
    label: {
      type: String,
    },
    placeholder: {
      type: String,
    },
  },
  // mounted() {
  //   // this.$refs.inputRef.$el.focus();
  // },
});

export default Component;
